import React from "react"
import Button from "../../../../Components/Button";
import correctingIcon from "../../../../Components/images/icon-correcting.svg";


class ProfileInputFullName extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            fullName: this.props.userFullName,
            error_fullName: '',
        };

        this.handleFullNameChange = this.handleFullNameChange.bind(this);
        this.handleSubmitFullName = this.handleSubmitFullName.bind(this);
    }

    handleFullNameChange(event) {
        this.setState({fullName: event.target.value});
    }

    handleSubmitFullName(event) {
        event.preventDefault();

        if ((this.state.fullName === "") || (this.state.fullName === undefined)) {
            this.setState({error_fullName: "ÐŸÐ¾Ð¶Ð°Ð»ÑƒÐ¹ÑÑ‚Ð°, Ð²Ð²ÐµÐ´Ð¸Ñ‚Ðµ Ð¸Ð¼Ñ"});
            this.setState({fullName: this.props.fullName});
            return;
        }

        if ((this.state.fullName.length > 40)) {
            this.setState({error_fullName: "Ð’Ð°ÑˆÐµ Ð¿Ð¾Ð»Ð½Ð¾Ðµ Ð¸Ð¼Ñ Ð½Ðµ Ð¼Ð¾Ð¶ÐµÑ‚ Ð±Ñ‹Ñ‚ÑŒ > 40 ÑÐ¸Ð¼Ð²Ð¾Ð»Ð¾Ð²"});
            return;
        }


        this.setState({error_fullName: ""});
        alert('ÐžÑ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð½Ð¾Ðµ FullName: ' + this.state.fullName);
    }


    render() {

        return (
            <form className="profile_form" onSubmit={(event) => this.handleSubmitFullName(event)}>
                <label htmlFor="userFullName" className="label_element">
                    <input id="userFullName" className="profile_form__input"
                           type="text"
                           name="fullName"
                           value={this.state.fullName}
                           onChange={(e) => this.handleFullNameChange(e)}
                    />
                    <span className="text_error">{this.state.error_fullName}</span>
                </label>
                <Button className="profile_form__change" type="submit">
                    <img src={correctingIcon} alt="Ð¸Ð·Ð¼ÐµÐ½Ð¸Ñ‚ÑŒ Ð¸Ð¼Ñ"/>
                </Button>
            </form>
        );
    }
}

export default ProfileInputFullName